"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { useSessionContext } from "../sessionProvider";

export default function LoginPage() {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const router = useRouter();

  /* const { login, logout } = useSessionContext(); */

  /* useEffect(() => {
    if(session){
      router.push("/")
      
    }
    console.log(session)
  }, [login, session]); */

  async function handleSubmit(e) {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const res = await fetch("http://localhost/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });

      if (!res.ok) {
        const err = await res.json();
        throw new Error(err.message || "Erreur connexion");
      }

      const data = await res.json();

      // Utiliser la fonction login pour mettre Ã  jour le contexte + localStorage
      login(data.user); // Assure-toi que data.user contient les infos utilisateur

      // Si tu veux aussi stocker un token, tu peux faire ici :
      localStorage.setItem("token", data.token);
    } catch (e) {
      setError(e.message);
    } finally {
      setLoading(false);
    }
  }

  return (
    <form onSubmit={handleSubmit} style={{ maxWidth: 300, margin: "auto" }}>
      <h1>Connexion</h1>
      <input
        type="email"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
        required
      />
      <input
        type="password"
        placeholder="Mot de passe"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
        required
      />
      <button type="submit">{loading ? "Connexion..." : "Se connecter"}</button>
      {error && <p style={{ color: "red" }}>{error}</p>}
    </form>
  );
}
